<h1 id="sop-and-cors" tabindex="-1"><a class="header-anchor" href="#sop-and-cors" aria-hidden="true">#</a> SOP and CORS</h1>
<p>So, in your client-side JavaScript code you want to send HTTP requests to another website/a REST API? To bad that the Same-Origin Policy tries to prevent you from doing that. But don't worry, Cross-Origin Resource Sharing gives you the tools you need to trump it. And here you'll also learn how you can use the <code>XMLHttpRequest</code> object to send the requests.</p>
